<template>
  <div class="part-two" id="part-three"></div>
</template>

<script>

// 引入基本模板
let echarts = require("echarts/lib/echarts");

export default {
  name: "partTwo",
  data() {
    return {};
  },
  mounted() {
    this.drawECharts();
  },
  methods: {
    drawECharts() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById("part-three"));

      myChart.setOption({
        // 标题
        title: {
          // 标题文本
          text: "技能分布图",
          // 标题样式
          textStyle: {
            color: "#fff"
          },
          // 标题位置
          x: "center"
        },
        // 移动显示
        tooltip: {
          trigger: "item",
          // 显示文字样式
          formatter: "{a} <br/>{b} : {d}%"
        },
        // 注记
        legend: {
          x: "center",
          y: "bottom",
          textStyle: {
            color: "#fff"
          },
          data: [ "JavaScript","CSS3", "HTML5", "微信小程序", "Vue", "Node",  "其他" ]
        },
        // 注记显示手柄
        calculable: true,
        // 图形系列
        series: [
          {
            name: "技能分布",
            type: "pie",
            radius: [20, 120],
            roseType: "area",
            /* labelLine:{
                length:0.05,
                show:true,
            }, */
            data: [
                { value: 30, name: "JavaScript" },
                { value: 15, name: "CSS3" },
                { value: 15, name: "HTML5" },
                { value: 25, name: "微信小程序" },
                { value: 25, name: "Vue" },
                { value: 8, name: "Node" },
                { value: 15, name: "其他" }
            ]
          }
        ],
        // 颜色调整
        color: ['#00bfff', '#00ffdd', '#207ffc', '#00aeff', "#00eeff",  "#0099ff", "#0066ff"]
      });
    }
  }
};
</script>

<style scoped>
.part-two {
  width: 100%;
  height: 450px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
}
</style>







